.facilities {
  // insert all the child css inside this parent class

  padding-bottom: 6vh;

  .page-title {
    position: relative;
    background-image: url("./images/footer/bg.jpeg");
    width: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    padding: 86px 0px 87px 0px;
  }
  .page-title:before {
    position: absolute;
    content: "";
    background: #000;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    opacity: 0.4;
  }
  .page-title .title {
    position: relative;
    font-size: 45px;
    font-family: "Montserrat", sans-serif;
    line-height: 52px;
    color: #fff;
    font-weight: 500;
    margin-bottom: -2px;
  }
  .bread-crumb,
  .bread-crumb a {
    position: relative;
    font-size: 15px;
    font-family: "Open Sans", sans-serif;
    line-height: 26px;
    font-weight: 600;
  }
  .bread-crumb a {
    color: #fff;
  }
  .bread-crumb span {
    color: #337ab7;
  }

  #feature {
    padding-top: 7vh;
    padding-left: 2vw;
    padding-right: 2vw;
  }
  h5 {
    font-size: large;
    font-weight: 600;
  }
}
