.login {
  /* Login content */

  .zlogn {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 78.5vh;
    background: url("./images/login/pic1.JPG");
    background-size: cover;
    background-position: center;
    font-family: sans-serif;
  }

  .zloginbox {
    width: 320px;
    height: 420px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    top: 55%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    padding: 50px 30px;
    margin-top: 10vh;
    z-index: 0;
  }

  .avatar {
    background-image: url("./images/login/avatar.png");
    width: 100px;
    height: 100px;
    background-size: cover;
    border-radius: 50%;
    position: absolute;
    top: -50px;
    left: calc(50% - 50px);
  }

  .h1-font {
    margin: 0;
    padding: 20px;
    text-align: center;
    font-size: 22px;
    color: white;
    font-weight: bold;
  }

  .text-p {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 17px;
    color: #fff;
  }

  .zloginbox input {
    width: 100%;
    margin-bottom: 20px;
  }

  .zloginbox input[type="text"],
  input[type="password"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
    color: #fff;
    font-size: 16px;
  }

  .zloginbox button {
    border: none;
    outline: none;
    height: 44px;
    background: #1b8ce3;
    color: #fff;
    font-size: 18px;
    border-radius: 20px;
    width: 100%;
  }
  .zloginbox button:hover {
    cursor: pointer;
    background: #67b4f1;
    color: black;
  }

  h6 {
    line-height: 44px;
    height: 44px;
    font-size: 1.8rem;
    color: snow;
    font-weight: 600;
  }

  .zloginbox a {
    text-decoration: none;
    font-size: 12px;
    line-height: 20px;
    color: darkgrey;
  }

  .zloginbox a:hover {
    color: #67b4f1;
  }
  .in {
    padding-left: 15px;
  }
  /*  Medium screens */
  @media only screen and (max-width: 1366px) {
    .zlogn {
      height: 90vh !important;
    }
    .zloginbox {
      margin-top: 15vh !important;
    }
  }

  /*  Small screens((all iteams) */
  @media only screen and (max-width: 850px) {
    .zlogn {
      margin-top: 0;
      height: 90vh;
      position: relative;
    }
    .zloginbox {
      top: 35%;
      margin-top: 20vh;
    }
  }

  @media only screen and (max-width: 350px) {
    .zloginbox {
      top: 35%;
      margin-top: 20vh;
    }
  }
}
