html {
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  margin-bottom: 10px;
  color: #222;
  margin-top: 0px;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  margin-bottom: 10px;
  color: #222;
  margin-top: 0px;
  transition: all 500ms ease;
}
p {
  position: relative;
  font-size: 15px;
  font-family: "Lato", sans-serif;
  line-height: 26px;
  font-weight: 400;
  color: #848484;
  margin-bottom: 10px;
  transition: all 500ms ease;
}
.showicon {
  position: fixed;
  bottom: 3vh;
  color: white;
  background-color: #337ab7;
  bottom: 15px;
  right: 15px;
  font-size: 22px;
  line-height: 43px;
  width: 45px;
  height: 45px;
  background: #337ab7;
  text-align: center;
  z-index: 100;
}
.main-header {
  position: relative;
  left: 0px;
  top: 0px;
  z-index: 999;
  width: 100%;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}

.main-headerh {
  position: relative;
  left: 0px;
  top: 0px;
  z-index: 9999999999999999999999999 !important;
  width: 100%;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.containerh {
  padding: 0px 11.4%;
}
input,
button,
select,
textarea {
  font-family: "Hind", sans-serif;
}

.main_headerh ul,
.main_headerh li {
  list-style: none;
  padding: 0px;
  margin: 0px;
  z-index: 999;
}

.header-upperh {
  position: relative;
  background: #232428;
  width: 100%;
  padding: 9px 0px;
}
.header-upperh .top-lefth {
  position: relative;
  display: inline-block;
}
.header-upperh .top-lefth li {
  position: relative;
  display: inline-block;
  font-size: 12px;
  color: #d3d7ce;
  top: 1px;
  margin-right: 45px;
}
.header-upperh .top-lefth li:before {
  position: absolute;
  content: "";
  background: #656669;
  width: 1px;
  height: 20px;
  right: -30px;
  top: 3px;
}
.header-upperh .top-lefth li:last-child:before {
  display: none;
}
.header-upperh .top-lefth li i:before {
  font-size: 14px;
  margin-right: 7px;
}
.header-upperh .top-righth {
  position: relative;
  float: right;
  margin-right: 0.8%;
}
.header-upperh .top-righth .social-toph {
  position: relative;
  display: inline-block;
  margin-left: 14px;
  top: 1px;
}

.header-upperh .top-righth .social-toph li a {
  position: relative;
  display: inline-block;
  color: #cfcfcf;
  line-height: 26px;
  font-size: 14px;
}
.header-upperh .top-righth .social-toph li a:hover {
  color: #337ab7;
}
.header-upperh .top-righth .social-toph li {
  position: relative;
  display: inline-block;
  margin: 0px 0px 0px 26px;
}
.logo-boxh {
  background: url(./images/logo/logo1.png) no-repeat;
  height: 62px;
  background-size: 100% 100%;
  margin-top: 27px;
  margin-left: 30vw;
}
.logo-boxh a {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.header-upperh .search-box-areah .search-toggleh i:before {
  font-size: 14px;
  color: #cfcfcf;
  margin: 0px;
  transition: all 500ms ease;
}
.header-upperh .search-box-areah .search-toggleh:hover i:before {
  color: #337ab7;
  transition: all 500ms ease;
}
.header-upperh .search-box-areah {
  position: relative;
  display: inline-block;
  cursor: pointer;
  margin-right: -8px;
  top: 1px;
}
.header-upperh .search-box-areah:before {
  position: absolute;
  content: "";
  background: #656669;
  width: 1px;
  height: 20px;
  top: 3px;
  right: -21px;
}
.header-upperh .search-box-areah .search-boxh {
  opacity: 0;
  position: absolute;
  right: 0px;
  top: 35px;
  transition: all 500ms ease;
  visibility: hidden;
}

.header-upperh .search-box-areah {
  position: relative;
}
.header-upperh .search-box-areah .form-grouph button {
  position: absolute;
  top: 2px;
  right: 0px;
  width: 46px;
  height: 46px;
  color: #fff;
  background: #337ab7;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border: none;
  z-index: 10;
}
.header-upperh .search-box-areah input {
  width: 250px;
  height: 50px;
  border: 2px solid #337ab7;
  padding: 10px 20px;
  border-radius: 5px;
  z-index: 9;
}
.search-box-areah .search-boxh.now-visible {
  top: 35px;
  right: 0px;
  opacity: 1;
  visibility: visible;
  z-index: 9;
  transition: all 500ms ease;
}
.header-lowerh {
  position: relative;
  width: 100%;
  height: 15vh;
  background: #fff;
  z-index: 0;
  padding-bottom: 3vh;
  display: flex;
  flex-direction: row;
}

.logo-boxh {
  background: url(./images/logo/logo1.png) no-repeat;
  height: 9.2vh;
  background-size: 100% 100%;
  margin-top: 4vh;
  margin-bottom: 4vh;
  margin-left: 10vw;
  width: 18.3vw;
}
.logo-boxh a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.container1h {
  width: 50%;
}
.icon {
  width: 20px;
  height: 20px;
}
@media (min-width: 1200px) {
  .container {
    padding: 0px 0px;
  }
}
.container2h {
  width: 50%;
}
.navh {
  height: 100%;
}
.navh:after {
  content: "";
  clear: both;
  display: table;
}
.navh ul {
  padding-top: 3vh;
  text-align: center;
  list-style: none;
  position: relative;
}
.navh ul li {
  display: inline-block;
  margin: 0 5px;
}
.navh ul li a {
  color: rgb(71, 71, 71);
  line-height: 70px;
  text-decoration: none;
  font-size: 16.5px;
  padding: 8px 15px;
  font-family: "Open Sans", sans-serif;
}
.navh ul li a:hover {
  color: #337ab7;
}
.navh ul ul li a:hover {
  box-shadow: none;
}
.navh ul ul {
  padding-top: 0;
  position: absolute;
  top: 90px;
  border-top: 3px solid #337ab7;
  visibility: hidden;
  transition: top 0.3s;
  background: #222;
  opacity: 0;
  transition: 0.3s;
}
.navh ul ul ul {
  border-top: none;
}
.navh ul li:hover > ul {
  top: 70px;
  opacity: 1;
  visibility: visible;
}
.navh ul ul li {
  text-align: left;
  position: relative;
  margin: 0px;
  width: 200px;
  float: none;
  display: list-item;
  border-bottom: 1px solid #337ab7;
}
.navh ul ul li a {
  font-size: 15px;
  font-weight: bold;
  line-height: 50px;
  color: #fff;
}

.showh,
.iconh,
.inph {
  display: none;
}
.fa-plush {
  font-size: 15px;
  margin-left: 40px;
}
#blueh {
  color: #337ab7;
  z-index: 999;
}

.sticky-header {
  position: fixed;
  visibility: none;
  left: 0px;
  top: 0px;
  width: 100%;
  background: #fff;
  box-shadow: 0 0px 0 2px rgba(238, 238, 238, 0.66);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 9999999999999 !important;
}
.fixed-header .sticky-header {
  z-index: 999;
  -ms-animation-name: fadeInDown;
  -moz-animation-name: fadeInDown;
  -op-animation-name: fadeInDown;
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -ms-animation-duration: 500ms;
  -moz-animation-duration: 500ms;
  -op-animation-duration: 500ms;
  -webkit-animation-duration: 500ms;
  animation-duration: 500ms;
  -ms-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
  -op-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -ms-animation-iteration-count: 1;
  -moz-animation-iteration-count: 1;
  -op-animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}
.sticky-header .main-menu .navigation > li > a {
  padding: 27px 0px 22px 12px;
}
.sticky-header .logo-box {
  margin-top: 5px;
}
.sticky-header .main-menu .navbar-collapse > ul li.dropdown .dropdown-btn {
  top: 25px;
}

.sticky-header .main-menu {
  right: 25px;
}
.menu-bar .btn-box {
  position: relative;
  display: inline-block;
  right: -2px;
  top: -3px;
}
.main-menu .navbar-collapse {
  padding: 0px;
}

.main-menu .navigation {
  position: relative;
  margin: 0px;
}

.main-menu .navigation > li {
  position: relative;
  display: inline-block;
  margin-left: 36px;
}

.main-menu .navigation > li > a > i:before {
  margin-right: 5px;
  font-size: 16px;
}
.main-menu .navigation > li > a {
  position: relative;
  display: block;
  font-size: 16px;
  color: #222;
  line-height: 26px;
  padding: 42px 0px 42px 12px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  opacity: 1;
  --webkit-transition: all 500ms ease;
}

.main-menu .navigation > li:hover > a,
.main-menu .navigation > li.current > a,
.main-menu .navbar-collapse > ul li.dropdown:hover .dropdown-btn span:before,
.main-menu .navbar-collapse > ul li.dropdown.current .dropdown-btn span:before {
  color: #337ab7;
  transition: all 500ms ease;
}

.main-menu .navigation > li:hover > a:after {
  opacity: 1;
}

.main-menu .navigation > li > ul {
  position: absolute;
  left: 0px;
  top: 130%;
  margin-top: 0px;
  width: 220px;
  padding: 0px;
  z-index: 100;
  display: none;
  background: #222;
  border-top: 3px solid #337ab7;
  transition: all 1000ms ease;
  -moz-transition: all 1000ms ease;
  -webkit-transition: all 1000ms ease;
  -ms-transition: all 1000ms ease;
  -o-transition: all 1000ms ease;
}
.main-menu .navigation > li > ul > li {
  position: relative;
  width: 100%;
}
.main-menu .navigation > li > ul > li:last-child {
  border-bottom: none;
}

.main-menu .navigation > li > ul > li > a {
  position: relative;
  display: block;
  padding: 10px 15px;
  line-height: 28px;
  font-weight: 600;
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  text-transform: capitalize;
  color: #ffffff;
  border-bottom: 1px solid #337ab7;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.main-menu .navigation > li > ul > li:last-child > a {
  border-bottom: none;
}

.main-menu .navigation > li > ul > li:hover > a {
  color: #337ab7;
  padding: 10px 15px 10px 17px;
  transition: all 500ms ease;
}

.main-menu .navigation > li > ul > li.dropdown:hover > a:after {
  color: #ffffff;
  transition: all 500ms ease;
}

.main-menu .navigation > li.dropdown:hover > ul {
  visibility: visible;
  opacity: 1;
  top: 100%;
  transition: all 500ms ease;
}

.main-menu .navbar-collapse > ul li.dropdown .dropdown-btn {
  display: inline-block;
  position: absolute;
  top: 42px;
  right: -15px;
  cursor: pointer;
}
.main-menu .navbar-collapse > ul li.dropdown .dropdown-btn span:before {
  color: #222;
  font-size: 14px;
}

/** main-footer-section **/
.sec-pad {
  padding: 96px 0px 100px 0px;
}

.main-footer {
  position: relative;
