/* Small screens */
@media only screen and (max-width: 760px) {
  .containerh {
    padding: 0px 0px;
    text-align: center;
    align-items: center;
  }
  .header-upperh {
    z-index: 999999999;
  }
  .header-lowerh {
    z-index: 99999999;
  }
  .header-upperh .top-righth {
    float: none;
    margin: auto;
  }
  .search-box-areah .search-boxh.now-visible,
  .header-upperh .search-box-areah .search-boxh {
    right: -200px;
  }
  .header-upperh .top-lefth li {
    margin: 0 auto;
    width: auto;
    display: block;
  }
  .header-upperh .top-lefth li:before {
    display: none;
  }
  .header-upperh .top-lefth {
    display: block;
  }
  .fixed .logo-boxh {
    display: none;
  }
  .menu-barh .btn-boxh {
    top: 3px;
  }
  .header-upperh,
  .header-lowerh {
    width: 100%;
  }
  .header-lowerh {
    flex-direction: column;
  }
  .container1h {
    width: 100%;
  }
  .logo-boxh {
    background: url(./images/logo/logo1.png) no-repeat;
    height: 10vh;
    background-size: 100% 100%;
    margin-top: 3vh;
    margin-bottom: 4vh;
    margin-left: 36%;
    width: 50vw;
  }
  .container2h {
    width: 100%;
    background: #ffffff !important;
  }
  .navh ul {
    width: 100%;
    margin-right: 0px;
    margin-top: -2vh !important;
    text-align: left;
    background: #ffffff !important;
    padding-top: 0;
  }
  .showh + a,
  .navh ul {
    display: none;
  }
  .navh ul li,
  .navh ul ul li {
    display: block;
    width: 100%;
  }
  .navh ul li a:hover {
    box-shadow: gray;
  }
  .showh {
    display: block;
    font-size: 16px;
    line-height: 70px;
    cursor: pointer;
  }

  .iconh {
    display: block;
    color: #337ab7;
    position: absolute;
    top: 0;
    right: 40px;
    line-height: 70px;
    cursor: pointer;
    font-size: 25px;
  }
  .navh ul li {
    border: 1px solid rgb(226, 226, 226);
    padding-left: 5vw;
    width: auto;
  }

  .navh ul ul {
    top: 70px;
    border-top: 0px;
    float: none;
    position: static;
    display: none;
    opacity: 1;
    visibility: visible;
    text-align: left;
    background: #fff;
  }
  .navh ul ul li {
    margin-left: -3vw;
    width: auto;
  }
  .navh ul ul li a {
    font-size: 13px;
    color: black;
  }
  .navh ul ul a {
    padding-left: 40px;
  }
  .navh ul ul ul a {
    padding-left: 80px;
  }
  .navh ul ul ul li {
    position: static;
  }
  #btn:checked + ul,
  #btnh:checked + ul,
  #btn-1:checked + ul,
  #btn-2:checked + ul {
    display: block;
  }

  .navh ul ul li {
    border-bottom: 0px;
  }
  .navh ul li a {
    padding: 8px 0;
    font-size: 16px;
  }
  .sticky-header {
    display: none;
  }
}
@media only screen and (max-width: 1000px) {
  .logo-boxh {
    margin-left: 10vw;
  }
}
