.galary {
  html {
    overflow-y: scroll;
  }
  body {
    font-family: "sans-serif" "Roboto";
    font-size: 16px;
    margin: 0;
    padding: 0;
  }
  img {
    width: 30vw;
    height: auto;
  }
  a {
    text-decoration: none;
  }

  .title-head {
    font-family: "Montserrat", sans-serif;
    text-align: center;
    position: relative;
    font-size: 40px;
    line-height: 37px;
    color: #848484;
  }

  .title-head span {
    color: #337ab7;
    font-size: 40px;

    line-height: 37px;
  }
  .title-head h1 {
    color: #222;
  }
  .sub_head {
    font-family: "Montserrat", sans-serif;
  }
  .featured-icon {
    width: 100%;
    text-align: center;
  }
  .icon {
    width: 90px;
  }
  .featured_nav {
    text-align: center;

    font-size: 18px;
    line-height: 27px;
  }
  .featured_nav a {
    color: #848484;
    padding: 1.5vw;
    text-decoration: none;
  }
  .featured_nav a:hover {
    color: lightblue;
    padding: 1.5vw;
  }
  .featured_grid_container {
    margin: 30px 10%;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
    grid-gap: 20px;
  }
  .featured_grid_container::before {
    display: none;
  }
  .featured_grid {
    position: relative;
    text-align: center;
    cursor: default;
    width: 100% !important;
    height: auto;
  }
  .libimage {
    width: 100% !important;
    height: auto;
    transition-duration: 500ms;
  }
  .grid_a {
    display: block;
    position: absolute;
    top: 0px;
    color: transparent;
    margin-bottom: -12px;
    width: 90%;
    height: 100%;
  }
  .grid_a:hover {
    width: inherit;
    display: block;
    color: white;
    text-decoration: none;
    transition: ease-in-out;
    transition-duration: 500ms;
    box-shadow: 50% 10% inset;
  }

  .grid_a h3 {
    margin-top: 40%;
    font-size: 2rem;
  }

  @media screen and (max-width: 1000px) {
    .featured_nav {
      font-size: xx-large;
    }
  }
}

.grid_a h3 {
  margin-top: 50%;
  font-size: 2rem;
  color: inherit;
}
