.contact {
  .page-title {
    position: relative;
    background-image: url("./images/footer/bg.jpeg");
    width: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    padding: 86px 0px 87px 0px;
  }
  .page-title:before {
    position: absolute;
    content: "";
    background: #000;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    opacity: 0.4;
  }
  .page-title .title {
    position: relative;
    font-size: 45px;
    font-family: "Montserrat", sans-serif;
    line-height: 52px;
    color: #fff;
    font-weight: 500;
    margin-bottom: -2px;
  }
  .bread-crumb,
  .bread-crumb a {
    position: relative;
    font-size: 15px;
    font-family: "Open Sans", sans-serif;
    line-height: 26px;
    font-weight: 600;
  }
  .bread-crumb a {
    color: #fff;
  }
  .bread-crumb span {
    color: #337ab7;
  }

  // ====================================================================
  // contact page
  // ====================================================================

  // ***/
  .contact-title input {
    width: "100%";
  }

  .contact-section {
    position: relative;
  }
  .contact-section .contact-title {
    position: relative;
    font-size: 30px;
    font-family: "Montserrat", sans-serif;
    line-height: 37px;
    color: #222;
    margin-bottom: 22px;
  }
  .contact-section .contact-info .title {
    position: relative;
    font-size: 22px;
    line-height: 30px;
    font-family: "Montserrat", sans-serif;
    color: #222;
    margin-bottom: 8px;
  }
  .contact-section .contact-info .single-info {
    position: relative;
    padding: 0px 0px 0px 57px;
    margin-bottom: 28px;
  }
  .contact-section .contact-info .single-info .icon-box {
    position: absolute;
    left: 0px;
    top: 10px;
  }
  .contact-section .contact-info .single-info .icon-box i:before {
    font-size: 38px;
    color: #337ab7;
    margin: 0px;
  }
  .contact-section input {
    height: 45px;
    margin-bottom: 20px;
  }
  .contact-section textarea {
    height: 206px;
  }
  .contact-section .btn-one {
    padding: 12px 33px 12px 33px;
  }
  .contact-section .contact-info {
    position: relative;
    top: 59px;
  }
  .map-section .google-map-area {
    position: relative;
    margin-bottom: 0px;
  }
  #contact-google-map {
    height: 420px;
    width: 100%;
  }
  .con_map {
    border: 0;
  }
  .upp {
    margin-top: 5vh;
  }
}
