.home {
  .carousel-inner {
    max-height: 100vh !important;
  }
  .carousel_heading{
    position:absolute;
    animation-name: stretch;
    animation-duration: 2.5s; 
    animation-timing-function: ease-out; 
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: none;
 
    font-size: 5rem;
    z-index: 3;
    font-weight: bold;
    margin-top:18%;
    margin-left: 37%;
    color:white;
  }
  @keyframes stretch {
    0% {
      transform: scale(.3);
    }
    100% {
      transform: scale(1); 
    }
  }
  @media only screen and (max-width: 760px) {
  .carousel_heading{
    margin-top:20%;
    margin-left: 37%;
  }
  
}
  .slider_pic1,
  .slider_pic2,
  .slider_pic3 {
    width: 100%;
    height: 100%;
    top:-80% !important;
    filter: brightness(70%);
    overflow-x: hidden; /* Hide horizontal scrollbar */
    overflow-y: scroll;
    background-size: cover;
    -ms-background-size: cover;
    -o-background-size: cover;
    -moz-background-size: cover;
    -webkit-background-size: cover;
  }
  /** about section **/

  .about-section {
    position: relative;
    padding: 114px 0px 81px 0px;
  }
  .section-title h2 {
    font-size: 25px;
    color: #848484;
  }
  .about-section .text p {
    margin-bottom: 33px;
  }
  .about-section .btn-one {
    padding: 12px 38px 12px 39px;
    margin-right: 9px;
  }
  .about-section .btn-two {
    padding: 12px 38px 12px 39px;
    margin-left: 9px;
  }

  .featured-icon {
    width: 100%;
  }
  .icon {
    width: 90px;
  }
  .title-head {
    text-align: center;
    position: relative;
    font-size: 40px;
    line-height: 37px;
    color: #848484;
    width: 100%;
  }

  .title-head span {
    color: #337ab7;
    font-size: 40px;

    line-height: 37px;
  }
  .about-content text {
    line-height: 5px;
  }
  .about-content p {
    margin-top: 30px;
  }
  .video-gallery {
    width: 100%;
    height: auto;
    text-align: center;
    position: relative;
    overflow: hidden;
    margin-left: 8px;
    margin-top: -56px;
  }
  .video-gallery img {
    transform: scale(1);
    transition: all 0.5s ease 0s;
    width: 100%;
  }
  .video-gallery .overlay-gallery {
    background-color: transparent;
    bottom: 0;
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    transition: all 500ms ease;
  }
  .video-gallery .overlay-gallery .icon-holder {
    display: table;
    height: 100%;
    width: 100%;
  }
  .video-gallery .overlay-gallery .icon-holder .icon {
    display: table-cell;
    vertical-align: middle;
  }
  .video-gallery .overlay-gallery .icon-holder .icon a img {
    width: auto;
    transform: scale(1);
    transition: all 0.5s ease 0s;
  }

  .video-gallery:hover .overlay-gallery {
    background-color: rgba(78, 205, 102, 0.7);
  }
  .video-gallery:hover img {
    transform: scale(1.1);
  }
  .video-gallery .overlay-gallery .icon-holder .icon a:hover img {
    transform: scale(1.1);
  }

  /** cta-section **/
  .manager_msg {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .manager_pic {
    max-width: 30%;
    height: auto;
    filter: contrast(100%);
  }
  .cta-section {
    position: relative;
    width: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }
  .cta-section:before {
    position: absolute;
    content: "";
    background: rgba(0, 0, 0, 0.1);
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    opacity: 1;
  }
  .cta-section .section-title h2 {
    font-size: 44px;
    margin-bottom: 82px;
  }
  .cta-section .title-head {
    margin-bottom: 4px;
  }
  .white .section-title h3 {
    color: black;
    text-align: justify;
  }
  .white .title-head {
    color: #337ab7;
    margin-bottom: 16px;
  }
  .white .section-title h2:before {
    background: url("./images/home/title-shap.png");
    margin-left: -50px;
    bottom: -53px;
  }
  .cta-section .btn-one {
    font-size: 15px;
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    padding: 12px 34px 12px 34px;
  }
  .text-primary {
    margin-top: 5px;
  }
}
